.page-header {
  width: 100%;
  box-sizing: border-box;
}

.page-header__container {
  display: flex;
  width: 73.9%;
  height: auto;
  margin: 0 auto;
  padding-top: 35px;
  box-sizing: border-box;
}

@media (max-width: $desktop-width) {
  .page-header {
    width: 100%;
    //min-width: 768px;
  }

  .page-header__container {
    width: 100%;
    padding-left: 30px;
    padding-right: 30px;
    padding-top: 20px;
  }
}

@media (max-width: $tablet-width) {
  .page-header__container {
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 15px;
  }
}
